body{
  margin:0px;
}

.laser-parent-div{
  height: 130vh;
}

.laser-top-panel{
  padding-top: 15px;
  padding-left: 50px;
  padding-right: 50px;
  height: 50px;
  width: 100%;
  color: #FFFFFF;
  background-color: #2E86C1;
}

.laser-blue-text{
  color: #2E86C1;
}

.laser-red-text{
  color: #A93226;
}

.laser-controls{
  margin-left: auto;
  margin-right: 0px;
  float: right;
  margin-top: -7px;
}

.laser-inline{
  display: inline;
}

.laser-100-width{
  max-width: 100px;
}

.laser-150-width{
  max-width: 150px;
}

.laser-side-bar{
  position: absolute;
  right: 0;
  z-index: 100;
  min-height: 100vh;
  width: 350px;
  max-width: 350px;
  margin-left: auto;
  margin-right: 0px;
  background-color: #FFFFFF;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 16px;
  padding-top: 8px;
  margin-top: 50px;
}

.close_btn{
  text-align: end;
  height: 30px;
}

.close_btn:hover{
  cursor: pointer;
}

.laser-shadow{
  -webkit-box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);
}

.laser-latest-div{
  position: absolute;
  left: 0;
  z-index: 100;
  height: 60vh;
  min-height: 400px;
  width: 250px;
  max-width: 250px;
  margin-left: 0px;
  margin-right: auto;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 16px;
  padding-top: 8px;
  margin-top: 50px;
  background-color: rgba(0,0,0,0.3);
}